import React, { useState, useEffect } from "react";
import CurtainList from "./components/CurtainList.js";
import fetchQuote from "./helpers/api.js";
import randomColor from "./helpers/randomColor.js";

export default function App() {
	const [quotes, setQuotes] = useState([]);

	const fetchNextQuote = () => {
		var quotesCopy = [...quotes];
		var newQuote = {
			loading: true,
			color: randomColor(),
			key: null,
			quoteText: null,
			quoteAuthor: null,
			quoteGenre: null,
		};
		var newQuotes = [...quotesCopy, newQuote];
		setQuotes(newQuotes);
		fetchQuote().then((randomQuote) => {
			newQuote = {
				...newQuote,
				...randomQuote.data[0],
				loading: false,
				key: randomQuote.data[0]._id,
			};
			newQuotes = [...quotesCopy, newQuote];
			setQuotes(newQuotes);
		});
	};

	useEffect(() => {
		fetchNextQuote();
	}, []);

	const TITLE = "Random Quote Generator";

	return <CurtainList quotes={quotes} onClick={() => fetchNextQuote()} />;
}
